<template>
  <div class="container">
    <h1>Latest Posts</h1>
    <form v-on:submit.prevent="createPost">
      <label for="create-post">Title</label>: 
    <input type="text" id="create-post" name="title" v-model="title" placeholder="Add title"><br><br>
    <label for="create-post">Content</label>: 
    <textarea id="create-post" name="body" v-model="body" placeholder="Add content"></textarea>
    <br>
    <button type="submit">Add Post</button>
    </form>
    
    <!-- Create Post here -->
    <hr>
    <p class="error" v-if="error">{{error}}</p>
    <div class="post-container">
      <div class="post"
        v-for="(post, index) in posts"
        v-bind:item="post"
        v-bind:index="index"
        v-bind:key="post._id"
        v-on:dblclick="deletePost(post._id)"
      >
      {{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}` }}
        <h4 class="text">{{ post.title }}</h4>
        <p class="text"> {{ post.body }} </p>
      </div>
    </div>
  </div>  
</template>

<script>
import PostService from "../PostService.js";

export default {
  name: "PostComponent",
  data() {
    return {
      posts: [],
      error: "",
      title: "",
      body: ""
    };
  },

  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch (err) {
      this.error = err.message;
    }
  }, 
  
  methods: {
    async createPost(fields){
      const data = {
        title: fields.target.elements.title.value,
        body: fields.target.elements.body.value
      }
      
      await PostService.insertPost(data);
      this.posts = await PostService.getPosts();
    },
  
  async deletePost(id){

      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    }
  }
};
</script>





